<template>
    <div class="timeline-wrap">
        <div class="row">
            <div class="col-md-8 col-md-offset-2">
              <div class="timeline">
                <div>
                  <div class="y-m-block" v-for="(item,i) in postListForTime" :key="i">
                    <ul class="y-m-d-block" >
                      <li class="leader">
                        <span class="timeline-date">
                            <span>{{  item[0].add_time | time('MM/YYYY') }}</span>
                        </span>
                      </li>
                      <li class="item" v-for="(sub_item, i) in item" :key="i">
                        <div class="content-wrap" >
                          <span class="timeline-date">{{ sub_item.add_time | time('DD') }}</span>
                          <router-link :to="{ path: '/blog/post/'+sub_item.id }">
                             <span class="content">{{sub_item.title}}</span>
                          </router-link>
                        </div> 
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
        </div>
    </div>
</template>

<script>
import {
    getTimeline,
} from '../../../../api/blog/index'
import moment from 'moment';
import gsap from 'gsap';
export default {
    data(){
        return {
            postList: [],
            postListForTime: [],
        }
    },
    methods: {
      loadAnime(){
        let tl = gsap.timeline();
        tl.from('.timeline', {
          duration: 0.6,
          opacity: 0,
          y: 10,
        })
        // tl.from('.y-m-block .leader', {// 头
        //   duration: 0.3,
        //   y: '0',
        //   opacity:0,
        //   stagger: 0.05,
        //   ease: 'Power0.easeInOut'
        // }).addLabel("taoya");
        // tl.from('.y-m-block  .timeline-date', { // 线
        //   duration: 0.1,
        //   opacity: 0,
        //   x: -200,
        //   stagger: 0.05,
        // }, 'taoya');
        // tl.from('.timeline .item .content', { // 内容
        //   x: 400, 
        //   duration: 0.6,
        //   opacity:0,
        //   stagger: 0.01,
        //   ease: 'Power0.easeInOut'
        // },'taoya-=0.05')
      },
      loadPost(){
        getTimeline().then(res=>{
            this.postList = res.data;
            this.placeOnArticle();
        })
      },
        placeOnArticle(){
            let step1 = this.postList
            .filter(item => {
                return item.add_time; 
            })
            .reduce((prev, cur, index) => {
                let formatDate = moment(cur.add_time).format('YYYY-MM');
                cur.date = formatDate;
                if (!prev[formatDate]) prev[formatDate] = [];

                prev[formatDate].push(cur);

                return prev;
            }, {});

            let step2 = Object.keys(step1)
            .sort((a,b) => moment(b).valueOf() - moment(a).valueOf())
            .reduce((prev, cur) => {
                prev[cur] = step1[cur];
                return prev;
            }, {});

            let res =  Object.values(step2)
            .reduce((prev, cur) => {
                prev.push(cur.sort((a,b) => moment(b.add_time).valueOf() - moment(a.add_time).valueOf()));
                return prev;
            }, []);

            this.postListForTime = res;

            this.$nextTick(()=>{
              this.loadAnime();
            })
        }
    },
    mounted(){
        this.loadPost();
    }
}
</script>
